import React from 'react';
import { useTranslation } from 'react-i18next';
import ImgFace1 from '../../Img/ImageFace1.jpg';

export const HomeScreen = () => {

    const [ t ] = useTranslation('global');

    return (
        <div id='home' className='screen home'>
            <div className='home-description'>
                <h1> { t('home.Text1') } </h1>
                <h1> { t('home.Text2') } </h1>
                <h3> { t('home.Text3') } </h3>
                <a 
                    target='_blank'
                    className='btn'
                    href='https://drive.google.com/file/d/1YQ72ky4ic1ofs3sjhbJTvJnhzKFmRQA9/view?usp=sharing' 
                >
                    CV
                </a>
                <img 
                    src='https://res.cloudinary.com/dxzcqv92g/image/upload/v1634078458/kjeycctakluagocmvjwi.png' 
                    alt='img-logo'
                />
            </div>
            <img
                src={ ImgFace1 }
                alt='image-face-1' 
            />
        </div>
    )
}
